(function () {
  'use strict'

  angular
        .module('App')
        .factory('FlightDelayService', FlightDelayService)

  FlightDelayService.$inject = ['$http', 'DATA_URL', 'DATA_INFO', '$q', '$localForage']

  function FlightDelayService ($http, DATA_URL, DATA_INFO, $q, $localForage) {
    var flightDelayData

        /**
         * Split csv file contents into array of strings starting from a specifed index.
         * @param {Number} row - row to start slice from.
         * @param {String} res - Multiine string representing csv file contents.
         * @returns {Array} = Array of strings, each line representing an row in the file.
         */
    function getDataAsArray (row, res) {
      var data = res.data
      return data.split('\n').slice(row).filter(function (line) {
        return line
      })
    }

        /**
         * Converts an array of strings into array of objects.
         * @param {Array} data- Array of strings.
         * @param {String} separator - split strings by.
         * @returns {Array} = Array of objects.
         */
    function convertDataToJson (seperator, data) {
      seperator = seperator || ','

      function getTimeAsMinuteOfDay (str) {
                // get minutes of the day, 12:00 => 720 minute of the day
        var arr = str.match(/.{1,2}/g).map(Number)
        var hour = arr[0]
        var min = arr[1]
        return !hour ? min : (hour * 60) + min
      }

      function filterString (str) {
        return str.replace(/"/g, '')
      }

      function getDelayRatio (arrivalDelay, elapsedTime) {
                // delay ratio is arrival delay in minutes / flight elapsed time in minutes
        return (arrivalDelay / elapsedTime) * 100
      }
            // map through all the array entries, converting each entry to a corresponding object.
      return data.map(function (line) {
        var cols = line.split(seperator)
        var flightDate = new Date(cols[DATA_INFO.FLIGHT_DATE_COL])
        var day = flightDate.getDay()

        var distance = Number(cols[DATA_INFO.DISTANCE_COL])
        var departureTime = cols[DATA_INFO.DEP_TIME_COL]

        departureTime = filterString(departureTime)
        var departureTimeMin = getTimeAsMinuteOfDay(departureTime)

        var arrivalTime = cols[DATA_INFO.ARR_TIME_COL]
        arrivalTime = filterString(arrivalTime)
        var arrivalTimeMin = getTimeAsMinuteOfDay(arrivalTime)

        var elapsedTime = Number(cols[DATA_INFO.ELAPSED_TIME_COL])

        var arrivalDelay = Number(cols[DATA_INFO.ARR_DELAY_COL])
        var delayed = arrivalDelay > 0   // if its an arrival delay the precalculate its delayRatio
        var delayRatio = delayed ? getDelayRatio(arrivalDelay, elapsedTime) : 0

        var origin = String(cols[DATA_INFO.ORIGIN_COL])
        origin = filterString(origin)

        var destination = String(cols[DATA_INFO.DEST_COL])
        destination = filterString(destination)

        return {
          flightDate: flightDate,
          day: day,
          origin: origin,
          destination: destination,
          departureTime: departureTime,
          departureTimeMin: departureTimeMin,
          arrivalTime: arrivalTime,
          arrivalTimeMin: arrivalTimeMin,
          arrivalDelay: arrivalDelay,
          elapsedTime: elapsedTime,
          distance: distance,
          delayed: delayed,
          delayRatio: delayRatio
        }
      })
    }

        /**
         * Enrich data with airport name list.
         * @param {Array} data- Array of flight schedule objects.
         * @returns {Object} = Object composed of array flight schedule and airport name list.
         */
    function addAirPortData (data) {
      var res = {}
      data.forEach(function (item) {
        if (!res[item.origin]) {
          res[item.origin] = true
        }
        if (!res[item.destination]) {
          res[item.destination] = true
        }
      })
      var airports = Object.keys(res)
      return { data: data, airports: airports }
    }

        /**
         * Called to get flight delay data from memory or from local database.
         * @returns {Promise} = Promise will resolve to flight delay data or reject when not found locally.
         */
    function getFlightDelayDataLocal () {
      if (flightDelayData) { // if data exists as private variable resolve with it
        return $q.when(flightDelayData)
      } else {
        return $localForage.getItem('flightData').then(function (data) {
          if (!data) {  // if not found in local forage, reject with an error
            return $q.reject(new Error('data not found'))
          }
          flightDelayData = data  // Set data in private variable and resolve with it
          return $q.when(flightDelayData)
        })
      }
    }

        /**
         * Called to get data from remote when data does not exists locally.
         * @returns {Promise} = Promise will resolve with data or reject with error.
         */
    function getFlightDelayDataRemote () {
      return $http.get(DATA_URL)
                .then(getDataAsArray.bind(null, DATA_INFO.START_INDEX))
                .then(convertDataToJson.bind(null, DATA_INFO.SEPERATOR))
                .then(addAirPortData)
                .then(cacheData)
                .catch($q.reject)
    }

        /**
         * Caches data locally in local database and in a private variable.
         * @returns {Promise} = Promise will resolves with data, or rejects with error.
         */
    function cacheData (data) {
      return $localForage.setItem('flightData', data)
                .then(function (res) {
                  flightDelayData = res // store in instance variable
                  return flightDelayData
                })
    }

        /**
         * Queries for flight delays.
         * @param {Object} query - Represents selected data points, day, origin and destination.
         * @param {String} data - Flight delays data.
         * @returns {Object} = Object representing delay  data, delay ratio data, average delay ratio, max delay ratio.
         */
    function queryFlightDelays (query, data) {
      var delayTimeData = []
      var delayRatioData = []
      var totalDelayRatio = 0
      var maxDelayRatio = 0
      data.filter(function (item) { // if delayed and it matches query parameters
        return item.delayed &&
                    item.day === query.day.id &&
                    item.destination === query.destination &&
                    item.origin === query.origin
      })
                .forEach(function (item) {
                  delayTimeData.push(item.departureTimeMin) // add time the flight departed to its list
                  delayRatioData.push(item.delayRatio) // add delay ratio(precalculated) to its list
                  totalDelayRatio += item.delayRatio   // add to total delay ratio used for calculating average delay ratio
                  if (item.delayRatio > maxDelayRatio) { // keep track of the max delay ratio, used for ploting histogram
                    maxDelayRatio = item.delayRatio
                  }
                })
            // get average delay ratio
      var averageDelayRatio = !delayRatioData.length ? 0 : Math.round((totalDelayRatio / delayRatioData.length) * 100) / 100
      maxDelayRatio = Math.round(maxDelayRatio)
      return { delayTimeData: delayTimeData, delayRatioData: delayRatioData, averageDelayRatio: averageDelayRatio, maxDelayRatio: maxDelayRatio }
    }

        /**
         * Called to get data, check data locally first, if not found check remotely.
         * @returns {Promise} = Promise resolves with data or reject.
         */
    function getFlightDelayData () {
      return getFlightDelayDataLocal()
                .catch(getFlightDelayDataRemote)
    }

        /**
         * Queries for flight delays.
         * @param {Object} query - Represents selected data points, day, origin.
         * @param {String} data - Flight delays data.
         * @returns {Object} = Object representing list of delays and corresponding list of distance.
         */
    function getDelaysByDistance (query, data) {
      var delays = []
      var distances = []
      data.filter(function (item) {
        return item.delayed && item.day === query.day.id && item.origin === query.origin
      }).forEach(function (item) {
        delays.push(item.arrivalDelay)
        distances.push(item.distance)
      })
      return { delays: delays, distances: distances }
    }

    return {
      getFlightDelayData: getFlightDelayData,
      queryFlightDelays: queryFlightDelays,
      getDelaysByDistance: getDelaysByDistance
    }
  }
})()
;
(function () {
  'use strict'

  angular
        .module('App')
        .controller('FlightDelaysController', FlightDelaysController)

  FlightDelaysController.$inject = ['$timeout', 'WEEK_DAYS', 'FlightDelayService', 'FlightDelayChartService', 'flightDelayData', 'cfpLoadingBar']

  function FlightDelaysController ($timeout, WEEK_DAYS, FlightDelayService, FlightDelayChartService, flightDelayData, cfpLoadingBar) {
    var vm = this
    vm.flightDelayData = flightDelayData
    vm.selected = { day: '', origin: '', destination: '' } // keep track of search parameters

    vm.weekDays = WEEK_DAYS

    var chartOptions = {
      arrivalDelayTime: { // keep track of arrival delays chart options
        bins: 24,
        max: 0,
        intervals: 60,
        xAxis: 'Hour of the day (departure time)',
        yAxis: 'Frequency of arrival delays'
      },
      arrivalDelayRatio: { // keep track of arrival delay ratio chart options.
        xAxisFormat: null,
        bins: 10,
        max: 0,
        intervals: 10,
        xAxis: 'Delay ratio in %',
        yAxis: 'Frequency of arrival delay  ratio',
        drawLine: { value: 0, text: 'Average(%) is ', chartId: 'ratios' }
      }
    }

    function preBind (interval) { // prebind interval to x axis chart tick
      return function (d) {
        return Math.round(d * interval)
      }
    }

    vm.chartData = {} //
    vm.histogramOptions = {}

    vm.enableSearch = function () { // enable search button.
      return Object.keys(vm.selected).every(function (key) {
        return vm.selected[key]
      })
    }

    vm.search = function () {
      cfpLoadingBar.start()

      $timeout(function () {
        cfpLoadingBar.complete()
        // get data that matches the current selection
        var delayData = FlightDelayService.queryFlightDelays(vm.selected, vm.flightDelayData.data)
            // set delay chart properties
        var arrivalData = getArrivalDelayHistogramData(delayData.delayTimeData, chartOptions.arrivalDelayTime)
        vm.chartData.arrivalDelaytime = arrivalData.chartData
        chartOptions.arrivalDelayTime.max = arrivalData.max // set chart max y axis option

        vm.histogramOptions.arrivalDelayTime = FlightDelayChartService.getHistogramOptions(chartOptions.arrivalDelayTime)

        var averageDelayRatio = delayData.averageDelayRatio
        var maxDelayRatio = delayData.maxDelayRatio
        chartOptions.arrivalDelayRatio.drawLine.value = averageDelayRatio // set the value of the average delay ratio
            // set interval here based on maxDelayRatio
            // maxDelayRatio/bins
        var delayRatioChartInterval = maxDelayRatio ? maxDelayRatio / chartOptions.arrivalDelayRatio.bins : 10
        chartOptions.arrivalDelayRatio.intervals = delayRatioChartInterval
            // prebind intervals into xAxis formatter
        chartOptions.arrivalDelayRatio.xAxisFormat = preBind(chartOptions.arrivalDelayRatio.intervals)

            // set delay ratio chart properties
        var delayRatioData = getArrivalDelayHistogramData(delayData.delayRatioData, chartOptions.arrivalDelayRatio)
        vm.chartData.arrivalDelayRatio = delayRatioData.chartData
        chartOptions.arrivalDelayRatio.max = delayRatioData.max
        vm.histogramOptions.arrivalDelayRatio = FlightDelayChartService.getHistogramOptions(chartOptions.arrivalDelayRatio)
      }, 2000)
    }

        /**
         * Get the required histogram data. called by both histogram charts
         * @param {Object} data- Chart data.
         * @param {Object}  options-  options.
         * @returns {Object} = Object with  Chart Data and max y axis value.
         */
    function getArrivalDelayHistogramData (data, options) {
      var range = [0, options.bins * options.intervals] // range on the xaxis
      var binData = d3.layout.histogram().bins(options.bins).range(range)(data)
      var chartData = { bar: true, values: [] }
      var localMaxY = 0
      binData.forEach(function (item) {
        if (localMaxY < item.y) {
          localMaxY = item.y
        }
        chartData.values.push([item.x, item.y])
      })
      return { chartData: [chartData], max: localMaxY }
    }
  }
})()
;
(function () {
  'use strict'

  angular
        .module('App')
        .factory('FlightDelayChartService', FlightDelayChartService)

  FlightDelayChartService.$inject = []

  function FlightDelayChartService () {
        /**
         * Get the options required for a histogram.
         * @param {Object} options- option.
         * @returns {Object} = Chart option.
         */
    function getHistogramOptions (options) {
      function getYDomain () {
        return options.max ? [0, options.max] : [0]
      }

      var yDomain = getYDomain()
      var histogramOptions = {
        chart: {
          type: 'historicalBarChart',
          margin: {
            top: 20,
            right: 20,
            bottom: 65,
            left: 50
          },
          useInteractiveGuideline: false,
          callback: function (chart) {
            if (options.drawLine && options.drawLine.value) {
              var xScale = chart.xAxis.scale()
              var margin = chart.margin()
              var svg = d3.select('#' + options.drawLine.chartId + ' svg')
              var height = chart.height()
              var onXAxis = xScale(options.drawLine.value) / options.intervals
              svg.append('line')
                                .style('stroke', '#FF7F0E')
                                .style('stroke-width', '2.5px')
                                .attr('x1', onXAxis + margin.left)
                                .attr('y1', margin.top)
                                .attr('x2', onXAxis + margin.left)
                                .attr('y2', height - margin.bottom)
              svg.append('text')
                                .attr('x', onXAxis + margin.left)
                                .attr('y', margin.top)
                                .text(options.drawLine.text + '' + options.drawLine.value)
            }
          },
          height: 300,
          x: function (d, i) {
            return i
          },
          y: function (d) {
            return d[1]
          },
          showValues: true,
          valueFormat: function (d) {
            return d3.format(',.1f')(d)
          },
          duration: 100,
          xAxis: {
            axisLabel: options.xAxis,
            tickFormat: function (d) {
              var format = options.xAxisFormat
              return format && angular.isFunction(format) ? format(d) : d
            },
            ticks: options.bins

          },
          yAxis: {
            axisLabel: options.yAxis,
            axisLabelDistance: -10,
            tickFormat: function (d) {
              return d3.format(',.1f')(d)
            }
          },
          yDomain: yDomain
        }
      }
      return histogramOptions
    }

    function getScatterChartOptions (options) {
      var scatterChartOptions = {
        chart: {
          type: 'scatterChart',
          height: 400,

          showDistX: true,
          showDistY: true,
          tooltipContent: function (key) {
            return '<h3>' + key + '</h3>'
          },
          duration: 100,
          useInteractiveGuideline: true,
          xAxis: {
            axisLabel: options.xAxis
          },
          yAxis: {
            axisLabel: options.yAxis,
            tickFormat: function (d) {
              return d3.format('.02f')(d)
            },
            axisLabelDistance: -5
          }
        }
      }
      return scatterChartOptions
    }
    return {
      getHistogramOptions: getHistogramOptions,
      getScatterChartOptions: getScatterChartOptions
    }
  }
})()
;
(function () {
  'use strict'

  angular
    .module('App')
    .controller('FlightDelaysAnalysisController', FlightDelaysAnalysisController)

  FlightDelaysAnalysisController.$inject = ['$timeout', 'WEEK_DAYS', 'FlightDelayService', 'FlightDelayChartService', 'flightDelayData', 'cfpLoadingBar']

  function FlightDelaysAnalysisController ($timeout, WEEK_DAYS, FlightDelayService, FlightDelayChartService, flightDelayData, cfpLoadingBar) {
    var vm = this
    vm.chartOptions = { xAxis: 'Distance in miles', yAxis: 'Arrival delay in minutes' }
    vm.analysis = {}
    vm.weekDays = WEEK_DAYS
    vm.flightDelayData = flightDelayData

    vm.selected = { day: '', origin: '' }

    vm.enableSearch = function () { // enable search button.
      return Object.keys(vm.selected).every(function (key) {
        return vm.selected[key]
      })
    }

    vm.search = function () {
      cfpLoadingBar.start()
      $timeout(function () {
        cfpLoadingBar.complete()
        // get delays and corresponding distances for selected params
        var data = FlightDelayService.getDelaysByDistance(vm.selected, vm.flightDelayData.data)
        var chartData = getAnalysisData(data) // format as chart data
        vm.analysis.data = chartData
        vm.analysis.options = FlightDelayChartService.getScatterChartOptions(vm.chartOptions) // get chart params
      }, 1000)
    }
       /**
         * Get the required histogram data. called by both histogram charts
         * @param {Object} data- Object representing delays and distances.
         * @param {Object}  options-  options.
         * @returns {Object} = Object with  Chart Data.
         */
    function getAnalysisData (data) {
      var distances = data.distances
      var delays = data.delays
      var values = distances.map(function (distance, index) {
        return { x: distance, y: delays[index] }
      })

      return [{
        values: values,
        key: 'Arrival delay against distance',
        strokeWidth: 2,
        classed: 'dashed'
      }]
    }
  }
})()
;
